import React, { useState, useEffect } from "react";
import { LinkContainer } from "react-router-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Navbar, Nav, Container, NavDropdown } from "react-bootstrap";
import { logoutUser } from "../actions/userActions";
import "../styles/header.css";
import { QRCode } from "react-qrcode-logo";
import "reactjs-popup/dist/index.css";


import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import IconButton from '@mui/material/IconButton';
import EmailIcon from '@mui/icons-material/Email';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import ShareIcon from '@mui/icons-material/Share';
import InfoIcon from '@mui/icons-material/Info';
import QrCode2Icon from "@mui/icons-material/QrCode2";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

function AlertDialogSlide() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
       <IconButton  aria-label="upload picture" component="label" onClick={handleClickOpen}>
            <QrCode2Icon sx={{ color: "white", cursor: "pointer"}}/>
        </IconButton>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle>{"Share about Our work"}</DialogTitle>
        <DialogContent>
          <QRCode value={window.location.href} />
        </DialogContent>
        <DialogActions>
          <IconButton color="primary" aria-label="upload picture" component="label">
            <input hidden accept="image/*" type="file" />
            <EmailIcon sx={{ color: "red", cursor: "pointer"}}/>
          </IconButton>
          <IconButton color="primary" aria-label="upload picture" component="label">
            <input hidden accept="image/*" type="file" />
            <WhatsAppIcon sx={{ color: "green", cursor: "pointer" }}/>
          </IconButton>
          <IconButton color="primary" aria-label="upload picture" component="label">
            <input hidden accept="image/*" type="file" />
            <ShareIcon sx={{ cursor: "pointer" }}/>
          </IconButton>
        </DialogActions>
      </Dialog>
    </div>
  );
}


const Header = () => {
	const dispatch = useDispatch();
	const userLogin = useSelector((state) => state.userLogin);
	const cart = useSelector((state) => state.cart);
	const { userInfo } = userLogin;
	const { cartItems } = cart;

	const [show1, setShow1] = useState(false); // to close dropdown when clicking anywhere outside
	const [show2, setShow2] = useState(false); // to close dropdown when clicking anywhere outside
	const [count, setCount] = useState(0);

	// update count when new cart changes
	useEffect(() => {
		setCount(cartItems.reduce((acc, item) => acc + item.qty, 0));
	}, [cartItems]);

	// close the second dropdown when it is open and user clicks anywhere else
	const handleDropdown2 = (e) => {
		if (show2) {
			setShow2(false);
		} else {
			setShow2(true);
			setShow1(false);
		}
	};

	// close the first dropdown when it is open and user clicks anywhere else
	const handleDropdown1 = (e) => {
		if (show1) {
			setShow1(false);
		} else {
			setShow1(true);
			setShow2(false);
		}
	};

	// dispatch action to logout user
	const handleLogout = () => {
		dispatch(logoutUser());
		window.location.href = "/";
	};

  // render different navbars for large and small screens without navbar toggle
  return (
    <header>
      {/* this section covers entire screen except the dropdown, to handle onclicks */}
      <section
        className="navbar-dropdown-cover"
        style={{
          display:
            window.innerHeight > 430 && (show1 || show2) ? "block" : "none",
          minWidth: "100%",
          height: "100%",
          zIndex: "100",
          position: "absolute",
        }}
        onClick={() => {
          setShow1(false);
          setShow2(false);
        }}
      />

      {/* conditionally render different navbars for the mobile sreens */}
      <Navbar
        bg="primary"
        variant="dark"
        collapseOnSelect expand="lg"
      >
        <Container>
          <LinkContainer to="/">
            <Navbar.Brand>
              <img
								src="/images/image_low.png"
								className="nav-logo"
								alt="logo"
								heght="3rem"
							/>
              Bangalore Food Bank
            </Navbar.Brand>
            </LinkContainer>
           
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />

        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav
            className="ms-auto nav-mobile"
            style={
              userInfo
                ? {
                    justifyContent: "space-between",
                  }
                : {
                    justifyContent: "space-evenly",
                  }
            }
          >
            <LinkContainer to="/aboutUs" variant="primary">
                  <Nav.Link>
                  <i class="fa fa-info-circle" aria-hidden="true" /> About
                  </Nav.Link>
            </LinkContainer>
            <LinkContainer to="/campaigns" variant="primary">
              <AlertDialogSlide />
            </LinkContainer>
            {/* <LinkContainer to="/campaigns" variant="primary"> */}
              <Nav.Link onClick={() => window.location.replace("/#campaignsSection")}>
                  <i className="fa fa-university navbar-icons" /> Campaign
              </Nav.Link>
            {/* </LinkContainer> */}
            

            {userInfo && userInfo.isAdmin && (
              <>
                {/* display this only on mobile screens */}
                <LinkContainer
                  className="d-block d-md-none"
                  to="/admin/userlist"
                >
                  <Nav.Link>
                    <i className="fas fa-users" />
                  </Nav.Link>
                </LinkContainer>
                <LinkContainer
                  className="d-block d-md-none"
                  to="/admin/orderlist"
                >
                  <Nav.Link>
                    <i className="fas fa-user-shield" />
                  </Nav.Link>
                </LinkContainer>
                <LinkContainer
                  className="d-block d-md-none"
                  to="/admin/productlist"
                >
                  <Nav.Link>
                    <i className="fas fa-shopping-bag" />
                  </Nav.Link>
                </LinkContainer>
              </>
            )}
            {userInfo && (<LinkContainer to="/cart">
              <Nav.Link>
                {/* indicate cart size */}
                {count && window.innerWidth > 1000 ? (
                  <div className="nav-cart-size">
                    <span
                      style={
                        count > 10
                          ? { fontSize: "0.6em" }
                          : { fontSize: "0.7em" }
                      }
                    >
                      {count}
                    </span>
                  </div>
                ) : (
                  ""
                )}
                <i className="fas fa-shopping-cart navbar-icons" />{" "}
                {!(userInfo && userInfo.isAdmin) || window.innerWidth >= 430
                  ? "Donation"
                  : ""}
              </Nav.Link>
            </LinkContainer>)}
            {userInfo && userInfo.isAdmin && (
              // show this only on md screens and above
              <NavDropdown
                className="d-none d-md-block"
                title="Admin"
                id="adminMenu"
                show={show2}
                onClick={handleDropdown2}
              >
                <LinkContainer to="/admin/userlist">
                  <NavDropdown.Item>Users</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to="/admin/productlist">
                  <NavDropdown.Item>Products | Campaigns</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to="/admin/orderlist">
                  <NavDropdown.Item>Donations</NavDropdown.Item>
                </LinkContainer>
              </NavDropdown>
            )}
            {userInfo && (
              // show this only on mobile screens
              <Nav.Link className="d-block d-md-none" onClick={handleLogout}>
                <i className="fas fa-sign-out-alt navbar-icons" />{" "}
                {!(userInfo && userInfo.isAdmin) && "Logout"}
              </Nav.Link>
            )}
            {userInfo ? (
              <div className="nav-avatar-container">
                {/* show this container only on mobile screens */}
                {/* <LinkContainer to="/profile" className="d-block d-md-none">
                  <Nav.Link>
                    <img
                      // src={userInfo.avatar} 
                      src="https://w7.pngwing.com/pngs/81/570/png-transparent-profile-logo-computer-icons-user-user-blue-heroes-logo-thumbnail.png"
                      className="nav-avatar"
                      alt={userInfo.name}
                    />
                  </Nav.Link>
                </LinkContainer> */}
                <img
                  src={userInfo.profilephoto}
                  className="nav-avatar d-none d-md-block"
                  alt={userInfo.name}
                />

                {/* show this dropdown only on large screens */}
                <NavDropdown
                  className="d-none d-md-block"
                  title={userInfo.name}
                  id={userInfo.id}
                  show={show1}
                  onClick={handleDropdown1}
                >
                  <LinkContainer to="/profile">
                    <NavDropdown.Item>Profile</NavDropdown.Item>
                  </LinkContainer>
                  <NavDropdown.Item onClick={handleLogout}>
                    Logout
                  </NavDropdown.Item>
                </NavDropdown>
              </div>
            ) : (
              <LinkContainer to="/login" variant="primary" id="test-sign-in">
                <Nav.Link>
                  <i className="fas fa-user navbar-icons" /> Sign In
                </Nav.Link>
              </LinkContainer>
            )}
            
          </Nav>
              </Navbar.Collapse>
        </Container>
             

      </Navbar>
    </header>
  );
};

export default Header;
